import React from 'react'
import PText from '../components/PText';
import Button from '../components/Button';
import AboutImg from '../assets/images/aboutImg.png';
import styled from 'styled-components';
import AboutInfoItem from '../components/AboutInfoItem';
import ContactBanner from '../components/ContactBanner';


const AboutPageStyles = styled.div`
  padding: 20rem 0 10rem 0;
  .top-section {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }
  .left {
    flex: 3;
  }
  .right {
    flex: 2;
  }
  .about__subheading {
    font-size: 2.2rem;
    margin-bottom: 2rem;
    span {
      background-color: var(--deep-dark);
      padding: 0.5rem;
      border-radius: 8px;
    }
  }
  .about__heading {
    font-size: 3.6rem;
    margin-bottom: 3rem;
  }
  .about__info {
    margin-bottom: 4rem;
    .para {
      max-width: 100%;
    }
  }
  .right {
    img {
      border: 2px solid var(--gray-1);
    }
  }
  .about__info__items {
    margin-top: 15rem;
  }
  .about__info__item {
    margin-bottom: 10rem;
  }
  .about__info__heading {
    font-size: 3.6rem;
    text-transform: uppercase;
  }
  @media only screen and (max-width: 768px) {
    padding: 10rem 0;
    .top-section {
      flex-direction: column;
      gap: 5rem;
    }
    .about__subheading {
      font-size: 1.8rem;
    }
    .about__heading {
      font-size: 2.8rem;
    }
    .about__info__heading {
      font-size: 3rem;
    }
  }
`;



const About = () => {
    return (
        <AboutPageStyles>
           <div className="container">
               <div className="top-section">
                   <div className="left">
                    <p className="about__subheading">
                        Hi, I am <span>Raquel Muriega</span>
                    </p>
                    <h2 className="about__heading">
                        A freelance Web Developer
                    </h2>
                    <div className="about__info">
                    <PText>
                        I am from Buenos Aires, Argentina. A place of beauty and
                        nature. I love art and design. I always
                        try to design stuff with my unique point of view.
                        <br /> <br />
                        Coding is also an art for me. I love it and now I have the opportunity to design
                        along with the coding. I find it really interesting and I
                        enjoyed the process a lot.
                    </PText>
                    </div>
                    <Button btnText="Download CV" btnLink="#" />
                   </div>
                   <div className="right">
                        <img src={AboutImg} alt="Raquel img"/>
                   </div>
               </div>
               <div className="about__info__items">
                   <div className="about__info__item">
                        <h1 className="about__info__heading">
                            Education
                        </h1>
                        <AboutInfoItem 
                        title="School"
                        items={['Marco Sastre, Tigre, Buenos Aires']}
                        />
                        <AboutInfoItem 
                        title="Bootcamp"
                        items={['ADA, Buenos Aires, Argentina']}
                        />
                        <AboutInfoItem 
                        title="Bootcamp"
                        items={['CoderHouse, Buenos Aires, Argentina']}
                        />
                   </div>
                    
                   <div className="about__info__item">
                        <h1 className="about__info__heading">
                            My Skill
                        </h1>
                        <AboutInfoItem 
                        title="Frontend"
                        items={['HTML','CSS', 'JS', 'REACT']}
                        />
                        <AboutInfoItem 
                        title="Design"
                        items={['Photoshop','After Effects']}
                        />
                   </div>


                   <div className="about__info__item">
                        <h1 className="about__info__heading">
                            Experiences
                        </h1>
                        <AboutInfoItem 
                        title="2020-2021"
                        items={['Idílica Digital']}
                        />
                        <AboutInfoItem 
                        title="2019-2020"
                        items={['Freelance Developer']}
                        />
                   </div>
               </div>
           </div>

           <ContactBanner/>
        </AboutPageStyles>
    )
}

export default About
